.Container {
  width: 30%;
  display: flex;
  flex-flow: column;
  text-align: center;
  padding: 10px 20px 10px 20px;
  border-right: 1px solid white;
  align-items: flex-start;
}

@media (max-width: 800px) {
  .Container {
    width: 80%;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    border-right: none;
    border-bottom: 1px solid white;
  }
}
