.TableData {
    margin-top: 30px;
    margin-left: 0;
    text-align: left;
}

table {
    width: 200px;
    height: 200px;
    font-size: 20px;
    border-radius: 5px;
    color: white;
    font-weight: 700;
}

td {
    padding: 5px;
}

tr:nth-child(odd) {
    background-color: rgba(128, 128, 128, 0.288);
}

